
.stats-section{

    /* border: 2px solid green; */
    width: 80%;
    height: auto;
    margin: auto;
    padding: 10px;
    margin-top: 10px;
    border-radius: 12px;
    


}

.Stats-inside{
    /* border: 1px solid red; */
    background: radial-gradient(circle at 10% 20%, rgb(255, 229, 168) 0%, rgb(251, 174, 222) 100.7%);
    border-radius: 6px;
    padding: 10px;
    margin-top: 100px;
}
.stats-section h1{

    font-family: 'Chela One', cursive;
    word-spacing: 5px;
    letter-spacing: 2px;
    /* margin-top: 100px; */
}

.stats-container{
    /* border: 1px solid red; */
    height: auto;
    display: flex;
    justify-content: space-between;

}

.stats-container>div{
    width: 30%;
    height: 30%;
    border: 1px solid black;
    text-align: center;
    margin: auto    ;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 12px;


}

.calendar{
    /* border: 1px solid slateblue; */
    padding: 10px;
    height: 180px;
    margin: auto;
    width: 80%;

}
.regular{
    /* border: 1px solid red; */
    height: auto;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
   

}


.git-3 img{
  margin-left: 15px;
}

@media (min-width:300px) and (max-width:575px){

    .stats-container{
        display: block;
        width: auto;
       border: 1px solid blue;


    }
    .stats-container>div{
        width: auto;
        margin: 20px;
    }
    .regular{
        display: block;
        margin: auto;
    }
    .git-3 img{
        margin-left: 2px;
      }
}