.project-section{

    /* border: 1px solid green; */
    width: 80%;
    margin: auto;
    padding: 10px;
    margin-top: 10px;
    height: auto;
    border-radius: 12px;


}
.project-section h1{

    font-family: 'Chela One', cursive;
    word-spacing: 5px;
    letter-spacing: 2px;
    /* margin-top: 100px; */
}
.project-main-div{

    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: auto;
    gap: 10px;
    

}
.projects-inside{
    /* border: 1px solid red; */
    background: radial-gradient(circle at 10% 20%, rgb(255, 229, 168) 0%, rgb(251, 174, 222) 100.7%);
    border-radius: 6px;
    padding: 10px;
    margin-top: 100px;
}

.project-card{
    border: 1px solid black;
    border-radius: 12px;
    height: auto;
    margin: 20px;
    background-color:  rgb(238, 157, 184);
    

}


.project-img{
    border: 1px solid grey;
     margin: 20px;  
     background-color: white;
   
}

.project-img>img{
    height: 100%;
    width: 100%;
    
}

.project-title{
    margin: 20px;  

}
.project-description{
    margin: 20px;  

}
.project-techstack{
    margin: 20px;  

}
.project-description{
    margin: 20px;  

}
.deployed{
    margin: 20px;
    display: flex;
    justify-content: space-around;
}

@media  (min-width:300px) and (max-width:575px){

        .project-main-div{
            display: block;
            
        }
}